"use client";

import { useState, useRef, useEffect } from "react";
import { motion } from "motion/react";
import { ArrowDown } from "lucide-react";

export default function Hero() {
  const [isHovered, setIsHovered] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const heroRef = useRef<HTMLElement>(null);

  useEffect(() => {
    const handleScroll = () => setScrollY(window.scrollY);
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const parallaxOffset = scrollY * 0.5;
  const overlayOpacity = Math.min(scrollY / 500, 0.7);

  return (
    <section 
      ref={heroRef}
      className="fixed inset-0 h-screen overflow-hidden z-10"
    >
      {/* Background Images */}
      <div className="absolute inset-0">
        {/* Default Background - More Zoomed */}
        <motion.div
          initial={{ scale: 1.2 }}
          animate={{ 
            scale: isHovered ? 1 : 1.2,
            y: parallaxOffset
          }}
          transition={{ 
            scale: { duration: 2.5, ease: [0.25, 0.46, 0.45, 0.94] },
            y: { duration: 0, ease: "linear" }
          }}
          className="absolute inset-0"
        >
          <img
            src="/img/hero1.jpg"
            alt="Myrtle Pool House"
            className="w-full h-full "
          />
          <div className="absolute inset-0 bg-black/20" />
        </motion.div>

        {/* Hover Background - Smooth Transition */}
        <motion.div
          initial={{ y: "100%" }}
          animate={{ y: isHovered ? "0%" : "100%" }}
          transition={{ duration: 2.5, ease: [0.25, 0.46, 0.45, 0.94] }}
          className="absolute inset-0 z-10"
          style={{
            transform: `translateY(${parallaxOffset}px)`
          }}
        >
          <img
            src="/img/hero2.avif"
            alt="Myrtle Pool House Interior"
            className="w-full h-full object-cover scale-110"
          />
          <div className="absolute inset-0 bg-black/30" />
        </motion.div>
      </div>

      {/* Desktop Content */}
      <div className="relative z-20 h-full hidden lg:block">
        {/* Middle Row - Featured Project */}
        <div className="absolute top-1/2 left-0 right-0 -translate-y-1/2 px-8">
          <div className="flex items-center justify-between">
            {/* Left Side */}
            <motion.div
              initial={{ opacity: 0, x: -30 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 1, delay: 0.3, ease: [0.16, 1, 0.3, 1] }}
              className="flex items-center gap-8 text-white text-lg"
            >
              <span className="opacity-70">Featured Project</span>
              <span className="font-medium">Myrtle Pool House</span>
              <span className="opacity-70">2024</span>
            </motion.div>

            {/* Right Side */}
            <motion.button
              initial={{ opacity: 0, x: 30 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 1, delay: 0.5, ease: [0.16, 1, 0.3, 1] }}
              onHoverStart={() => setIsHovered(true)}
              onHoverEnd={() => setIsHovered(false)}
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              className="text-white text-lg font-medium relative group"
            >
              <span>View Project</span>
              <div className="absolute bottom-0 left-0 w-full h-0.5 bg-white transform origin-left scale-x-100 transition-transform duration-300 group-hover:scale-x-110"></div>
            </motion.button>
          </div>
        </div>

        {/* Bottom Left Text */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 1, delay: 0.7, ease: [0.16, 1, 0.3, 1] }}
          className="absolute bottom-8 left-8 text-white z-20"
        >
          <p className="text-xl leading-relaxed opacity-90 max-w-md">
            The OH Architecture style is defined by<br />
            strong, solid forms with subtle elegance,<br />
            natural balance and enduring appeal
          </p>
        </motion.div>

        {/* Bottom Right Scroll Indicator */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 1, delay: 0.9, ease: [0.16, 1, 0.3, 1] }}
          className="absolute bottom-8 right-8 text-white z-20"
        >
          <div className="flex items-center gap-2 text-sm opacity-70 uppercase tracking-wider">
            <span>(SCROLL DOWN)</span>
          </div>
        </motion.div>
      </div>

      {/* Mobile Content */}
      <div className="relative z-20 h-full lg:hidden">
        {/* Center Content */}
        <div className="h-full flex items-center justify-center px-4">
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 1, delay: 0.3, ease: [0.16, 1, 0.3, 1] }}
            className="text-center text-white"
          >
            <h1 className="text-4xl md:text-5xl font-light tracking-tight leading-tight mb-4">
              Myrtle Pool House
            </h1>
            <p className="text-xl md:text-2xl font-light mb-8 opacity-90">
              2024
            </p>
            
            <motion.button
              onHoverStart={() => setIsHovered(true)}
              onHoverEnd={() => setIsHovered(false)}
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.98 }}
              className="inline-flex items-center gap-3 px-8 py-4 bg-white/10 backdrop-blur-sm border border-white/20 rounded-full text-white font-medium hover:bg-white/20 transition-all duration-300"
            >
              View Project
              <ArrowDown className="w-4 h-4 rotate-[-45deg]" />
            </motion.button>
          </motion.div>
        </div>

        {/* Bottom Content - Left Aligned */}
        <div className="absolute bottom-8 left-4 right-4 text-white z-20">
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 1, delay: 0.7, ease: [0.16, 1, 0.3, 1] }}
            className="mb-6"
          >
            <p className="text-base leading-relaxed opacity-90 max-w-sm">
              The OH Architecture style is defined by<br />
              strong, solid forms with subtle elegance,<br />
              natural balance and enduring appeal
            </p>
          </motion.div>

          <motion.div
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 1, delay: 0.9, ease: [0.16, 1, 0.3, 1] }}
          >
            <div className="flex items-center gap-2 text-sm opacity-70 uppercase tracking-wider">
              <span>(SCROLL DOWN)</span>
            </div>
          </motion.div>
        </div>
      </div>

      {/* Parallax Scroll Overlay */}
      <motion.div
        id="hero-overlay"
        className="absolute inset-0 bg-black pointer-events-none z-30"
        style={{
          opacity: overlayOpacity
        }}
      />
    </section>
  );
}