"use client";

import { motion, useInView } from "motion/react";
import { useRef, useEffect } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

export default function About() {
const aboutRef = useRef<HTMLElement>(null);
const imageRef = useRef<HTMLDivElement>(null);
const isInView = useInView(aboutRef, { once: true, margin: "-100px" });

useEffect(() => {
const aboutSection = aboutRef.current;
if (!aboutSection) return;


// Scroll trigger for the hero darkening as about section comes into view
ScrollTrigger.create({
  trigger: aboutSection,
  start: "top bottom",
  end: "bottom top",
  scrub: 1,
  onUpdate: (self) => {
    // Darken hero section as about section scrolls over it
    const heroOverlay = document.querySelector('#hero-overlay');
    if (heroOverlay) {
      gsap.to(heroOverlay, {
        opacity: self.progress * 0.8,
        duration: 0.1
      });
    }
  }
});

return () => {
  ScrollTrigger.getAll().forEach(trigger => trigger.kill());
};


}, []);

return (
<section 
ref={aboutRef}
className="relative z-20 bg-neutral-50 px-4 lg:px-8 py-20 mt-[100vh]"
>
{/* Large Title - Top Left */}
<motion.div
initial={{ opacity: 0, y: 50 }}
animate={isInView ? { opacity: 1, y: 0 } : {}}
transition={{ duration: 1, delay: 0.2, ease: [0.16, 1, 0.3, 1] }}
className="mb-20"
>
<h2 className="text-[clamp(48px,8vw,160px)] font-medium  tracking-tight leading-none uppercase text-neutral-900">
Experience
<br />
focused design
</h2>
</motion.div>


  {/* Main Content Grid */}
  <div className="max-w-7xl mx-auto">
    <div className="grid grid-cols-12 gap-8 lg:gap-16">
      
      {/* Center Image */}
      <div className="col-span-12 lg:col-span-6 lg:col-start-4 mb-16 lg:mb-0">
        <motion.div
          ref={imageRef}
          initial={{ opacity: 0, scale: 0.9 }}
          animate={isInView ? { opacity: 1, scale: 1 } : {}}
          transition={{ duration: 1.2, delay: 0.4, ease: [0.16, 1, 0.3, 1] }}
          className="relative"
        >
          <img
            src="/img/about-center.jpg"
            alt="Architecture process"
            className="w-full aspect-[4/5] object-cover rounded-lg"
          />
        </motion.div>
      </div>

      {/* Text Content */}
      <div className="col-span-12">
        <div className="grid grid-cols-12 gap-8 lg:gap-16">
          
          {/* First Text Block */}
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            animate={isInView ? { opacity: 1, y: 0 } : {}}
            transition={{ duration: 1, delay: 0.6, ease: [0.16, 1, 0.3, 1] }}
            className="col-span-12 lg:col-span-6"
          >
            <p className="text-[clamp(16px,1.5vw,24px)] leading-relaxed text-neutral-700 font-light">
              We work closely with clients right from the start,
              with clear communication and expert guidance
              along the way. We also work closely with builders,
              consultants, and other partners to make sure each
              project runs smoothly and the final build delivers
              well beyond our shared aspirations.
            </p>
          </motion.div>

          {/* Learn More Button - Aligned with first text */}
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            animate={isInView ? { opacity: 1, y: 0 } : {}}
            transition={{ duration: 1, delay: 0.8, ease: [0.16, 1, 0.3, 1] }}
            className="col-span-12 lg:col-span-6 flex lg:justify-end"
          >
            <button className="bg-black text-white px-8 py-4 rounded-full font-bold text-sm uppercase tracking-wider hover:bg-neutral-800 transition-colors duration-300">
              Learn more about our studio
            </button>
          </motion.div>

          {/* Second Text Block */}
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            animate={isInView ? { opacity: 1, y: 0 } : {}}
            transition={{ duration: 1, delay: 1, ease: [0.16, 1, 0.3, 1] }}
            className="col-span-12 lg:col-span-6 mt-12"
          >
            <p className="text-[clamp(16px,1.5vw,24px)] leading-relaxed text-neutral-700 font-light">
              While our aesthetic is recognisable, each project
              evolves to embody its own shape and character,
              crafted in response to the aspirations of our clients,
              the opportunities of the site, and the creative vision
              of our architectural team.
            </p>
          </motion.div>

        </div>
      </div>

    </div>
  </div>
</section>


);
}